/* outfit-regular - latin */
@font-face {
  font-family: 'Outfit';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/outfit-v1-latin-regular.eot'); /* IE9 Compat Modes */
  src: local(''),
       url('../fonts/outfit-v1-latin-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
       url('../fonts/outfit-v1-latin-regular.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/outfit-v1-latin-regular.woff') format('woff'), /* Modern Browsers */
       url('../fonts/outfit-v1-latin-regular.ttf') format('truetype'), /* Safari, Android, iOS */
       url('../fonts/outfit-v1-latin-regular.svg#Outfit') format('svg'); /* Legacy iOS */
}
